header .top_nav{width: 100%; height: 50px; background: #343a40; box-shadow: 1px 1px 10px #000;}
header .top_nav > .left{float: left;}
header .top_nav > .left > button{background: none; box-shadow: none; border: none; cursor: pointer; margin: 17px 20px 16px 15px; width: 20px; height: 17px; position: relative;}
header .top_nav > .left > button > span{display: block; width: 20px; height: 3px; background: #fff; position: absolute; transition: .3s;}
header .top_nav > .left > button:hover span{background: #09d9fe;}
header .top_nav > .left > button > span:nth-of-type(1){top: 0; left: 0;}
header .top_nav > .left > button > span:nth-of-type(2){top: 7px; left: 0;}
header .top_nav > .left > button > span:nth-of-type(3){top: 14px; left: 0;}
.react_app.open header .top_nav > .left > button:hover > span:nth-of-type(1){-webkit-transform: rotate(-35deg);transform: rotate(-35deg); width: 11px; top: 3px;}
.react_app.open header .top_nav > .left > button:hover > span:nth-of-type(3){-webkit-transform: rotate(35deg);transform: rotate(35deg); width: 11px; top: 11px;}
.react_app.close_ header .top_nav > .left > button:hover > span:nth-of-type(3){-webkit-transform: rotate(-35deg);transform: rotate(-35deg); width: 11px; top: 11px; left: auto; right: 0;}
.react_app.close_ header .top_nav > .left > button:hover > span:nth-of-type(1){-webkit-transform: rotate(35deg);transform: rotate(35deg); width: 11px; top: 3px; left: auto; right: 0;}
header .top_nav > .left > a > img{width: 80px; display: inline-block; margin: 12px 0 11px 0;}
header .top_nav > .right{float: right; height: 20px; margin: 15px 30px 15px 0;}

header .top_nav > .right > a.message{display: inline-block; color: #fff; position: relative; margin-right: 10px; width: 20px; height: 20px; -webkit-transform: scaleX(-1); transform: scaleX(-1);}
header .top_nav > .right > a.message > svg{width: 17px; height: 17px; cursor: pointer;}
header .top_nav > .right > a.message > span{display: none; width: 6px; height: 6px; border-radius: 50%; background: red; position: absolute; top: 20%; left: 85%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%);}
header .top_nav > .right > a.message > span.new{display: block;}


header .top_nav > .right > a.auth{color: #fff; line-height: 20px; font-size: 1.2rem;}
header .top_nav > .right > a.auth:nth-of-type(1){color: #09d9fe; font-weight: bold;}
header .top_nav > .right > a.auth:hover{color: #09d9fe; transition: .5s; text-decoration: none;}
header .top_nav > .right > span:nth-of-type(1){display: inline-block; width: 1px; height: 10px; background: #fff; margin: 5px 8px;}
header .top_nav > .right > span:nth-of-type(2){color: #fff; line-height: 20px; font-size: 1.2rem; cursor: pointer;}

@charset "utf-8";

.left_nav{width: 240px; height: calc(100% - 50px); position: absolute; bottom: 0; left: 0; background: #212529; transition: .5s;}
.react_app.open .left_nav{-webkit-transform: translateX(0);transform: translateX(0);}
.react_app.close_ .left_nav{-webkit-transform: translateX(-100%);transform: translateX(-100%);}
