/*.loginBox*/
.login_wrap{width: 100%; height: calc(100% - 70px); position: relative;}
.login_wrap form{max-width: 410px; width: 90%; padding: 50px 30px; position: absolute; top: 40%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); border: 1px solid #fff;}
.login_wrap form:after{content: ""; display: block; clear: both;}
.login_wrap form > div{float: left; max-width: 260px; width: calc(100% - 88px);}
.login_wrap form > div > input{display: block; width: 100%; height: 40px; padding-left: 25px; font-weight: 300 !important; border: 1px solid #fff; background: transparent; color: #fff;}
.login_wrap form > div > input::-webkit-input-placeholder{color: #a0a0a0; font-size: 1.5rem; font-family: NanumBarunGothic; color: #eee !important;}
.login_wrap form > div > input:-ms-input-placeholder{color: #a0a0a0; font-size: 1.5rem; font-family: NanumBarunGothic; color: #eee !important;}
.login_wrap form > div > input::placeholder{color: #a0a0a0; font-size: 1.5rem; font-family: NanumBarunGothic; color: #eee !important;}
.login_wrap form > div > input:nth-of-type(1){margin-bottom: 10px;}
.login_wrap form > input{display: block; float: right; width: 70px; height: 90px; font-size: 1.24rem; color: #fff; background: none; line-height: 80px; border: none; cursor: pointer; transition: .2s; margin-bottom: 20px; border: 1px solid #fff;}
.login_wrap form > p{clear: both; margin-bottom: 0;}
.login_wrap form > p input#id_Save{display: none;}
.login_wrap form > p input#id_Save + label{position: relative; display: inline-block; width: 20px; height: 20px; cursor: pointer; background: url(data:image/png;base64,) no-repeat center; background-size: contain;}
.login_wrap form > p input#id_Save:checked + label{background: url(data:image/png;base64,) no-repeat center; background-size: contain;}
.login_wrap form > p input#id_Save + label span{position: absolute; width: 101px; font-size: 1.5rem; color: #fff; top: 1px; left: 26px; font-weight: 300 !important; }
.login_wrap form > span{display: inline-block; width: 100%; height: 1px; background: #b4b4b4; margin: 20px 0;}
.login_wrap form > a{border: 1px solid #fff;; display: block; width: 100%; background: none; height: 40px; border-radius: 7px; cursor: pointer; transition: .2s; font-weight: 300 !important; color: #fff; line-height: 40px; text-align: center; font-size: 1.3rem;}
.login_wrap form > a:hover{text-decoration: none;}
@charset "utf-8";

